.header {
  background-color: #131921;
  display: flex;
  align-items: center;
  top: 0px;
  position: -webkit-sticky;
  position: sticky;
  z-index: 101;
}
.header__logo {
  width: 100px;
  margin-top: 18px;
  margin-left: 10px;
  margin-right: 20px;
}
.header__search {
  display: flex;
  flex: 1 1;
  align-items: center;
}

.header__searchInput {
  height: 12px;
  padding: 10px;
  
  border-radius: 10px 0px 0px 10px;

  width: 100%;
  outline: red;
}
.header__searchInput:focus {
  border: 3px solid #febd69;
}
.bottom {
  font-weight: bolder;
}

.header__searchicon {
  background-color: #febd69;
  padding: 4px;
  height: 26px !important;
  border-radius: 0px 10px 10px 0px;
}
.header__nav {
  display: flex;
  justify-content: space-evenly;
  padding-right: 10px;
}
.header__link {
  display: flex;
  flex-direction: column;
  text-decoration: none;
}
.top {
  padding-top: 7px;
  color: white;
  font-size: 12px;
}
.bottom {
  color: white;
  font-size: 12px;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;

  margin-top: 5px;
}

.background_image {
  background-image: url("https://images-eu.ssl-images-amazon.com/images/G/31/AmazonVideo/2020/X-site/Multititle/Aug/1500x600_Hero-Tall_np._CB404803728_.jpg");
  width: 96%;
  height: 260px;
  background-size: cover;
  color: white;
  padding: 20px;
  -webkit-mask-image: linear-gradient(
